const ALL_FILES = gql`
  {
    files {
      edges {
        node {
          id
          name
          isAdmin
        }
      }
    }
  }
`;

function useQueryFake() {
  return {
    loading: false,
    error: false,
    data: {
      transactions
    }
  };
}

function TransactionsResourceList() {
  const [selectedItems, setSelectedItems] = useState([]);
  const [sortValue, setSortValue] = useState("DATE_MODIFIED_DESC");
  const [filterStatus, setFilterStatus] = useState("");
  const [taggedWith, setTaggedWith] = useState("");
  const [queryValue, setQueryValue] = useState("");

  const handleFilterStatusChange = useCallback(
    value => setFilterStatus(value),
    []
  );
  const handleFilterTaggedWithChange = useCallback(
    value => setTaggedWith(value),
    []
  );
  const handleQueryValueChange = useCallback(value => setQueryValue(value), []);
  const handleTaggedWithRemove = useCallback(() => setTaggedWith(""), []);
  const handleQueryValueRemove = useCallback(() => setQueryValue(""), []);
  const handleClearAll = useCallback(() => {
    handleTaggedWithRemove();
    handleQueryValueRemove();
  }, [handleQueryValueRemove, handleTaggedWithRemove]);

  const promotedBulkActions = [
    // {
    //   content: "Resend recovery email",
    //   onAction: () => console.log("Todo: implement bulk edit")
    // },
    // {
    //   content: "Edit accounts",
    //   onAction: () => console.log("Todo: implement bulk edit")
    // }
  ];

  const bulkActions = [
    // {
    //   content: "Add tags",
    //   onAction: () => console.log("Todo: implement bulk add tags")
    // },
    // {
    //   content: "Remove tags",
    //   onAction: () => console.log("Todo: implement bulk remove tags")
    // },
    {
      content: "Delete",
      onAction: () => console.log("Todo: implement bulk delete")
    }
  ];

  const filters = [
    // {
    //   key: "filterStatus",
    //   label: "Status",
    //   filter: (
    //     <ChoiceList
    //       title="Account status"
    //       titleHidden
    //       choices={[
    //         {label: 'Email sent', value: 'sent'},
    //         {label: 'Email not sent', value: 'notsent'},
    //         {label: 'Recovered', value: 'recovered'},
    //         {label: 'Not recovered', value: 'notrecovered'},
    //       ]}
    //       selected={filterStatus || []}
    //       onChange={handleFilterStatusChange}
    //       allowMultiple
    //     />
    //   ),
    //   shortcut: true
    // },
  ];

  const appliedFilters = !isEmpty(taggedWith)
    ? [
        {
          key: "taggedWith",
          label: disambiguateLabel("taggedWith", taggedWith),
          onRemove: handleTaggedWithRemove
        }
      ]
    : [];

  // const filterControl = (
  //   <Filters
  //     queryValue={queryValue}
  //     filters={filters}
  //     appliedFilters={appliedFilters}
  //     onQueryChange={handleQueryValueChange}
  //     onQueryClear={handleQueryValueRemove}
  //     onClearAll={handleClearAll}
  //   />
  // );

  const { loading, error, data } = useQueryFake(ALL_FILES);

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  function toPayoutStatusTag(status) {
    if (status === "paid") return <Badge>Paid</Badge>;
    if (status === "in_transit") return <Badge status="info">In transit</Badge>;
    if (status === "pending") return <Badge status="info">Pending</Badge>;
    return <Badge>Paid</Badge>;
  }

  function toTableHeader(transaction) {
    return [
      transaction[0], // date
      transaction[1], // type
      transaction[2], // order=account
      // transaction[3], // card brand
      // transaction[4], // card source
      transaction[5], // payout status
      transaction[6], // payout date
      // transaction[7], // available on
      transaction[8], // amount
      transaction[9], // fee
      transaction[10] // net
    ];
  }

  function toTableRow(transaction) {
    const t = toTableHeader(transaction);
    const date = t[4];
    return [
      t[0].replace(" -0400", ""),
      t[1],
      <Link url="/accounts/110">{t[2]}</Link>,
      toPayoutStatusTag(t[3]),
      <Link url={`/settings/payments/payouts/${date}`}>{date}</Link>,
      toCurrencyWithCents(t[5]),
      toCurrencyWithCents(-t[6]),
      <strong>{toCurrencyWithCents(t[7])}</strong>
    ];
  }

  const transactions = data.transactions.reverse();
  const headings = toTableHeader(
    "Transaction date,Type,Account,Card brand,Card source,Payout status,Payout date,Available On,Amount,Fee,Net"
      .split(",")
      .map(h => <strong>{h}</strong>)
  );

  return (
    <Card>
      <Card.Section>
        <Filters
          queryValue={queryValue}
          filters={filters}
          appliedFilters={appliedFilters}
          onQueryChange={handleQueryValueChange}
          onQueryClear={handleQueryValueRemove}
          onClearAll={handleClearAll}
        />
      </Card.Section>
      <DataTable
        columnContentTypes={[
          "text",
          "text",
          "text",
          "text",
          "text",
          "numeric",
          "numeric",
          "numeric"
        ]}
        headings={headings}
        rows={transactions.map(p => toTableRow(p.split(",")))}
        sortable={[true, true, false, false, true, false, false, true]}
        defaultSortDirection="descending"
        initialSortColumnIndex={0}
        onSort={() => {}}

        // totals={['', '', '', 255, '$155,830.00']}
      />
    </Card>
  );

  function renderItem(item) {
    const {
      id,
      name,
      url,
      size,
      email,
      isAdmin,
      account_status,
      account_plan,
      account_price,
      account_freq,
      ltv,
      update_time,
      create_time,
      tags,
      email_status,
      prospect_status
    } = item;
    const media = <Avatar customer size="medium" name={id} />;
    const shortcutActions = null;

    const accountPlan = `${account_plan}`;
    const accountPrice = toCurrency(account_price);
    const accountPayment = `${accountPrice}/${account_freq}`;

    const ltvDisplay = toCurrency(ltv);

    const prospectStatusDisplay = {
      recovered: {
        color: "success",
        label: "Recovered"
      },
      notrecovered: {
        color: "warning",
        label: "Not recovered"
      }
    };
    const prospectStatus = prospectStatusDisplay[prospect_status] || {};
    const emailStatusDisplay = {
      sent: {
        color: "success",
        label: "Sent"
      },
      notsent: {
        color: "warning",
        label: "Not sent"
      },
      scheduled: {
        color: "attention",
        label: "Scheduled"
      }
    };
    const emailStatus = emailStatusDisplay[email_status] || {};

    const tagsDisplay = (tags || []).map(t => <Badge>{t}</Badge>);

    return (
      <ResourceItem
        id={id}
        // url={`/accounts/${id}`}
        media={
          <div
            style={{
              height: 36,
              width: 50,
              display: "flex",
              alignItems: "center"
            }}
          >
            <img
              src={url}
              style={{ verticalAlign: "middle", maxHeight: 36, maxWidth: 50 }}
            />
          </div>
        }
        accessibilityLabel={`View details for ${email}`}
        // shortcutActions={shortcutActions}
        // persistActions
      >
        <div style={{ display: "flex", height: 36, alignItems: "center" }}>
          <div
            style={{
              width: "30%",
              overflow: "hidden",
              whiteSpace: "nowrap",
              textOverflow: "ellipsis"
            }}
          >
            <Link>
              <p style={{ verticalAlign: "middle" }}>{name}</p>
            </Link>
          </div>

          <div style={{ width: "50%" }}>
            <TextField value={url} disabled />
          </div>

          <div style={{ width: "15%", paddingLeft: 40 }}>{size}</div>

          <div style={{ width: "5%", textAlign: "right" }}>
            <Button icon={DeleteMinor} size="slim" />
          </div>
        </div>
      </ResourceItem>
    );
  }

  function disambiguateLabel(key, value) {
    switch (key) {
      case "taggedWith":
        return `Tagged with ${value}`;
      default:
        return value;
    }
  }

  function isEmpty(value) {
    if (Array.isArray(value)) {
      return value.length === 0;
    }
    return value === "" || value == null;
  }
}

export default function SettingsFileListPage() {
  const [selected, setSelected] = useState(0);

  const handleTabChange = useCallback(
    selectedTabIndex => setSelected(selectedTabIndex),
    []
  );

  return (
    <Page
      title="Transactions"
      secondaryActions={[
        {
          icon: () => <Icon source={ExportMinor} />,
          content: "Export"
          // url: "http://localhost:3000/leads/exportToCsv"
        }
      ]}
      breadcrumbs={[{ content: "Payouts", url: "/settings/payments/payouts" }]}
    >
      <Layout>
        <Layout.Section>
          <Card>
            <TransactionsResourceList />
          </Card>
        </Layout.Section>
      </Layout>
    </Page>
  );
}
