// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  ResourceItem,
  Link
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

import { UserAvatar, NotificationListAvatar } from "../lib/imageUtils";

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings($id: ID!, $json: String!) {
    updateOneSettingsItem(input: { id: $id, update: { json: $json } }) {
      id
      json
    }
  }
`;

export default function SettingsNotificationListPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading) {
    return (
      <SkeletonPage title="Preferences">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  }
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord =
    data && data.settingsItems.edges.length
      ? data.settingsItems.edges[0].node
      : {};
  return <SettingsNotificationListPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function renderItem(item) {
  const { id, name, description, status, optional } = item;
  const shortcutActions = null;

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-between",
        paddingTop: 8,
        paddingBottom: 8
      }}
    >
      <div style={{ width: "5%", verticalAlign: "top" }}>
        <Checkbox
          label=""
          onChange={value => {}}
          checked
          disabled={!optional}
        />
      </div>

      <div style={{ width: "30%", paddingTop: 4, paddingBottom: 4 }}>
        <h3>
          <Link url={`/settings/notifications/${id}`}>
            <TextStyle variation="strong">{name}</TextStyle>
          </Link>
        </h3>
      </div>

      <div style={{ width: "70%", paddingTop: 4, paddingBottom: 4 }}>
        {description}
      </div>
    </div>
  );
}

function SettingsNotificationListPageChild({ settingsRecord }) {
  const FIELDS = [
    // "name",  // read-only
    "title",
    "description",
    "domain",
    "domain_redirect",
    "google_analytics",
    "google_tag_manager",
    "password",
    "password_enabled",
    "password_message"
  ];
  let settingsData = {};
  try {
    settingsData = JSON.parse(settingsRecord.json);
  } catch (e) {
    // pass
  }

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, json: JSON.stringify(settings) }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const isDirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(isDirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  const resourceName = {
    singular: "notification",
    plural: "notifications"
  };

  return (
    <Page
      title="Notifications"
      breadcrumbs={[{ content: "Settings", url: "/settings" }]}
    >
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection
          title="Customer notifications"
          description="These notifications are automatically sent out to the customer. Click on the notification template to edit the content."
        >
          <Card title="Accounts" sectioned>
            <ResourceList
              resourceName={resourceName}
              items={[
                {
                  id: 1,
                  name: "New account",
                  description: "Sent when a new account is registered."
                },
                {
                  id: 2,
                  name: "Delete account",
                  description: "Sent to confirm the account has been deleted."
                }
              ]}
              renderItem={renderItem}
            />
          </Card>
          <Card title="Leads" sectioned>
            <ResourceList
              resourceName={resourceName}
              items={[
                {
                  id: 1,
                  name: "Thank you email",
                  description: "Sent when a lead leaves their email address."
                },
                {
                  id: 2,
                  name: "Incomplete registration reminder",
                  description: "Sent 1 day after an incomplete registration."
                }
              ]}
              renderItem={renderItem}
            />
          </Card>
        </Layout.AnnotatedSection>

        <Layout.AnnotatedSection
          title="Staff notifications"
          description="Choose the notifications that you want to receive."
        >
          <Card title="Staff notifications" sectioned>
            <ResourceList
              resourceName={resourceName}
              items={[
                {
                  id: 1,
                  name: "New account",
                  description: "Sent when a new account is registered.",
                  optional: true
                }
                // {id: 2, name: 'B', description: 'yyy'},
              ]}
              renderItem={renderItem}
            />
          </Card>
        </Layout.AnnotatedSection>
      </Layout>
    </Page>
  );
}
