// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  Link,
  Button
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "keys" } }) {
      edges {
        node {
          jwt_public_key
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings($id: ID!, $json: String!) {
    updateOneSettingsItem(input: { id: $id, update: { json: $json } }) {
      id
      json
    }
  }
`;

export default function DevelopersPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading) {
    return (
      <SkeletonPage title="Preferences">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  }
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord =
    data && data.settingsItems.edges.length
      ? data.settingsItems.edges[0].node
      : {};
  return <DevelopersPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function DevelopersPageChild({ settingsRecord }) {
  const FIELDS = [
    // "name",  // read-only
    "title",
    "jwt_public_key",
  ];
  const settingsData = settingsRecord;

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, ...settings }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const dirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(dirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  const apps = [
    {
      name: "Email/password",
      icon:
        "https://www.gstatic.com/mobilesdk/160409_mobilesdk/images/auth_service_email.svg",
      enabled: true
    },
    {
      name: "Google",
      icon:
        "https://www.gstatic.com/mobilesdk/160512_mobilesdk/auth_service_google.svg",
      enabled: true
    },
    {
      name: "Facebook",
      icon:
        "https://www.gstatic.com/mobilesdk/160409_mobilesdk/images/auth_service_facebook.svg"
    },
    {
      name: "Twitter",
      icon:
        "https://www.gstatic.com/mobilesdk/160409_mobilesdk/images/auth_service_twitter.svg"
    },
    {
      name: "Github",
      icon:
        "https://www.gstatic.com/mobilesdk/160409_mobilesdk/images/auth_service_github.svg"
    },
    {
      name: "Microsoft",
      icon:
        "https://www.gstatic.com/mobilesdk/170404_mobilesdk/auth_service_mslive.svg"
    },
    {
      name: "Apple",
      icon:
        "https://www.gstatic.com/mobilesdk/190910_mobilesdk/images/auth_service_apple.svg"
    }
  ];

  const keys = [
    {
      name: "read-only",
      client_id: "884d4d800eec25152f2c2d313e76f9c5"
    },
    {
      name: "read+write",
      client_id: "5b661b812eb9ad366049af12aaa4db48"
    }
  ];

  return (
    <Page title="Developers">
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection
          title="Domains"
          description="The primary domain is where the public site will be served, and the application domain is where users are redirected when they register or sign in."
        >
          <Card sectioned>
            <FormLayout>
              <TextField
                label="Primary domain"
                placeholder="beautifulsaas.com"
                value={settings.domain}
                onChange={value => handleFieldChange("domain", value)}
              />
              <TextField
                label="Application domain"
                placeholder={
                  settings.domain
                    ? `app.${settings.domain}`
                    : "app.beautifulsaas.com"
                }
                value={settings.domain_redirect}
                onChange={value => handleFieldChange("domain_redirect", value)}
              />
            </FormLayout>
          </Card>
          <Card sectioned>
            <FormLayout>
              <TextField
                label="Managed domain"
                placeholder={`${settings.name ||
                  "beautifulsaas"}.mysaasform.com`}
                disabled
              />
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.AnnotatedSection title="Authentication" description="">
          <Card>
            {apps.map(item => {
              const { name, icon, enabled } = item;

              return (
                <Card.Section>
                  <Stack alignment="center">
                    <Stack.Item>
                      <Link>
                        <img
                          src={icon}
                          alt={name}
                          style={{ width: 32, height: 32 }}
                        />
                      </Link>
                    </Stack.Item>
                    <Stack.Item fill>
                      <Link>
                        <strong>{name}</strong>
                      </Link>
                    </Stack.Item>
                    <Stack.Item>
                      {enabled ? (
                        <Link>
                          <TextStyle variation="positive">Enabled</TextStyle>
                        </Link>
                      ) : (
                        <Link>
                          <TextStyle variation="subdued">Disabled</TextStyle>
                        </Link>
                      )}
                    </Stack.Item>
                  </Stack>
                </Card.Section>
              );
            })}
          </Card>
        </Layout.AnnotatedSection>
        <Layout.AnnotatedSection title="Secrets & keys" description="">
          <Card sectioned>
            <FormLayout>
              <TextField
                label="JWT public key"
                multiline={4}
                disabled
                value={settings.jwt_public_key}
              />
              <TextField
                label="Webhook verification key"
                disabled
                type="password"
                value="dGhpcyBpcyBhIHRlbXAgdmFsdWUK"
                onChange={value =>
                  handleFieldChange("google_tag_manager", value)
                }
              />
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.AnnotatedSection title="API keys" description="">
          <Card primaryFooterAction={{ content: "Add" }}>
            {keys.map(item => {
              const { name, client_id } = item;

              return (
                <Card.Section>
                  <Stack alignment="center">
                    <Stack.Item>
                      <Link>
                        <strong>{name}</strong>
                      </Link>
                    </Stack.Item>
                    <Stack.Item fill>{client_id}</Stack.Item>
                    <Stack.Item>
                      <Button destructive plain>
                        Delete
                      </Button>
                    </Stack.Item>
                  </Stack>
                </Card.Section>
              );
            })}
          </Card>
        </Layout.AnnotatedSection>
        <Layout.Section>
          <PageActions
            primaryAction={{
              content: "Save",
              onAction: handleSave,
              disabled: !isDirty
            }}
          />
        </Layout.Section>
      </Layout>
    </Page>
  );
}
