// @ts-nocheck
import React, { useState, useCallback } from "react";
import { useParams } from "react-router-dom";
import { gql } from "apollo-boost";
import { useQuery } from "@apollo/react-hooks";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  ResourceItem,
  TextStyle,
  Stack,
  Button,
  List,
  TextContainer,
  ButtonGroup
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";

import { UserAvatar, StaffAvatar } from "../lib/imageUtils";
import { toCurrency, toShortDate } from "../lib/textUtils";
import { accounts } from "../data/accounts";

const QUERY_ACCOUNT = gql`
  query GetUser($id: ID!) {
    account(id: $id) {
      email
      isAdmin
    }
  }
`;

function useQueryFake(query, { variables: { id } }) {
  return {
    loading: false,
    error: false,
    data: {
      account: accounts.filter(i => i.id == id)[0] || accounts[0]
    }
  };
}

function AccountDetails({ account }) {
  const media = <Avatar customer size="medium" name={account.id} />;
  return (
    <Card title="Account details">
      <Card.Section></Card.Section>
    </Card>
  );
}

function UsersDetails({ account }) {
  return (
    <Card title="Users">
      <ResourceList
        items={account.users || []}
        renderItem={({ id, name, email, initials }) => (
          <ResourceItem
            id={id}
            url={`/users/${id}`}
            media={<UserAvatar email={email} />}
          >
            {name}
          </ResourceItem>
        )}
      />
    </Card>
  );
}

function AccountTimeline({ account }) {
  const [note, setNote] = useState("");

  const handleNoteChange = useCallback(value => setNote(value), []);

  const staff = <StaffAvatar email="" initials="EC" />;

  return (
    <Card title="Timeline" sectioned>
      <div style={{ marginBottom: 32 }}>
        <Stack alignment="center">
          {staff}
          <Stack.Item fill>
            <TextField
              placeholder="Add a note"
              fill
              value={note}
              onChange={handleNoteChange}
              connectedRight={<Button disabled={note === ""}>Post</Button>}
            />
          </Stack.Item>
        </Stack>
      </div>
      <List type="bullet">
        {(account.timeline || []).map((item, i) => (
          <List.Item key={i}>
            {toShortDate(item.create_time)} -{item.description}
          </List.Item>
        ))}
      </List>
    </Card>
  );
}

function AccountHighlights({ account }) {
  return (
    <Card title="Highlights" sectioned>
      
    </Card>
  );
}

function AccountBilling({ account }) {
  return (
    <Card title="Account information">
      <Card.Section title="Primary contacts">
        <p>SoloKeys Team</p>
        <Button plain>team@solokeys.com</Button>
        <br />
        <br />
        <p>Emanuele Cesena</p>
        <Button plain>ec@solokeys.com</Button>
        <br />
        <Button plain>(415) 403-8746</Button>
      </Card.Section>
      <Card.Section title="Billing information">
        <p>SoloKeys Inc.</p>
        <p>2035 Sunset Lake Road</p>
        <p>Suite B-2</p>
        <p>Newark, 19702, DE</p>
        <Button plain>finance@solokeys.com</Button>
        <p>PO: 3987347</p>
      </Card.Section>
    </Card>
  );
}

function AccountActions({ account }) {
  return (
    <Card title="Actions" subdued>
      <Card.Section title="Extend trial">
        <TextContainer>
          <p>
            Extend this account trail by another billing period.
          </p>
          <Button size="slim">Extend trial</Button>
        </TextContainer>
      </Card.Section>
      <Card.Section title="Apply discount">
        <TextContainer>
          <p>
            Apply a custom discount to the next bill.
          </p>
          <Button size="slim">Apply discount</Button>
        </TextContainer>
      </Card.Section>
      <Card.Section title="Pause account">
        <TextContainer>
          <p>
            Temporarily suspend the account. No user will be able to log in. You can reactivate it anytime.
          </p>
          <Button size="slim">Pause account</Button>
        </TextContainer>
      </Card.Section>
    </Card>
  );
}

function AccountTags({ account }) {
  return (
    <Card
      title="Tags"
      subdued
      sectioned
      actions={[{ content: "View all tags" }]}
    >
      <TextField placeholder="VIP, upsell, followup" />
    </Card>
  );
}

export default function AccountPage() {
  const { id } = useParams();
  const { loading, error, data } = useQueryFake(QUERY_ACCOUNT, {
    variables: { id: parseInt(id) }
  });

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  const { account } = data;

  return (
    <Page
      breadcrumbs={[{ content: "Accounts", url: "/accounts" }]}
      title={account.name}
      // primaryAction={{content: 'Save', disabled: true}}
      secondaryActions={[
        {
          content: "Resend verification email",
          icon: InviteMinor
        }
      ]}
      pagination={{
        hasPrevious: id < 110,
        hasNext: id > 101
      }}
    >
      <Layout>
        <Layout.Section>
          <AccountDetails account={account} />
          <UsersDetails account={account} />
          <AccountTimeline account={account} />
          <PageActions
            primaryAction={{ content: "Save" }}
            secondaryActions={[{ content: "Delete", destructive: true }]}
          />
        </Layout.Section>
        <Layout.Section secondary>
          <AccountHighlights account={account} />
          <AccountBilling account={account} />
          <AccountActions account={account} />
          <AccountTags account={account} />
        </Layout.Section>
      </Layout>
    </Page>
  );
}
