// @ts-nocheck
import React, { useState, useCallback } from "react";
import { gql } from "apollo-boost";
import { useQuery } from "@apollo/react-hooks";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  ChoiceList,
  RangeSlider,
  ResourceList,
  Filters,
  Avatar,
  TextStyle,
  Button,
  ResourceItem,
  Icon,
  Stack,
  Badge,
  Tabs
} from "@shopify/polaris";
import { ExportMinor, ImportMinor } from "@shopify/polaris-icons";

import { toCurrency, toShortDate } from "../lib/textUtils";
import { accounts } from "../data/accounts";

const ALL_ACCOUNTS = gql`
  {
    accounts {
      edges {
        node {
          id
          name
          isAdmin
        }
      }
    }
  }
`;

function useQueryFake() {
  return {
    loading: false,
    error: false,
    data: {
      accounts
    }
  };
}

function AccountsResourceList() {
  const [selectedItems, setSelectedItems] = useState([]);
  const [sortValue, setSortValue] = useState("DATE_MODIFIED_DESC");
  const [filterStatus, setFilterStatus] = useState("");
  const [taggedWith, setTaggedWith] = useState("");
  const [queryValue, setQueryValue] = useState("");

  const handleFilterStatusChange = useCallback(
    value => setFilterStatus(value),
    []
  );
  const handleFilterTaggedWithChange = useCallback(
    value => setTaggedWith(value),
    []
  );
  const handleQueryValueChange = useCallback(value => setQueryValue(value), []);
  const handleTaggedWithRemove = useCallback(() => setTaggedWith(""), []);
  const handleQueryValueRemove = useCallback(() => setQueryValue(""), []);
  const handleClearAll = useCallback(() => {
    handleTaggedWithRemove();
    handleQueryValueRemove();
  }, [handleQueryValueRemove, handleTaggedWithRemove]);

  const resourceName = {
    singular: "account",
    plural: "accounts"
  };

  const promotedBulkActions = [
    {
      content: "Extend trial",
      onAction: () => console.log("Todo: implement bulk edit")
    },
    {
      content: "Edit accounts",
      onAction: () => console.log("Todo: implement bulk edit")
    }
  ];

  const bulkActions = [
    {
      content: "Add tags",
      onAction: () => console.log("Todo: implement bulk add tags")
    },
    {
      content: "Remove tags",
      onAction: () => console.log("Todo: implement bulk remove tags")
    },
    {
      content: "Delete accounts",
      onAction: () => console.log("Todo: implement bulk delete")
    }
  ];

  const filters = [
    {
      key: "filterStatus",
      label: "Status",
      filter: (
        <ChoiceList
          title="Account status"
          titleHidden
          choices={[
            { label: "Trial", value: "trial" },
            { label: "Active", value: "active" },
            { label: "Past due", value: "pastdue" },
            { label: "Unpaid", value: "unpaid" },
            { label: "Paused", value: "paused" },
            { label: "Canceled", value: "canceled" }
          ]}
          selected={filterStatus || []}
          onChange={handleFilterStatusChange}
          allowMultiple
        />
      ),
      shortcut: true
    },
    {
      key: "taggedWith",
      label: "Tagged with",
      filter: (
        <TextField
          label="Tagged with"
          value={taggedWith}
          onChange={handleFilterTaggedWithChange}
          labelHidden
        />
      ),
      shortcut: true
    }
  ];

  const appliedFilters = !isEmpty(taggedWith)
    ? [
        {
          key: "taggedWith",
          label: disambiguateLabel("taggedWith", taggedWith),
          onRemove: handleTaggedWithRemove
        }
      ]
    : [];

  const filterControl = (
    <Filters
      queryValue={queryValue}
      filters={filters}
      appliedFilters={appliedFilters}
      onQueryChange={handleQueryValueChange}
      onQueryClear={handleQueryValueRemove}
      onClearAll={handleClearAll}
    >
      <div style={{ paddingLeft: "8px" }}>
        <Button onClick={() => console.log("New filter saved")}>Save</Button>
      </div>
    </Filters>
  );

  const { loading, error, data } = useQueryFake(ALL_ACCOUNTS);

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  return (
    <ResourceList
      resourceName={resourceName}
      items={data.accounts}
      renderItem={renderItem}
      selectedItems={selectedItems}
      onSelectionChange={setSelectedItems}
      promotedBulkActions={promotedBulkActions}
      bulkActions={bulkActions}
      sortValue={sortValue}
      sortOptions={[
        { label: "Latest update", value: "DATE_MODIFIED_DESC" },
        { label: "Newest", value: "DATE_CREATED_DESC" },
        { label: "Oldest", value: "DATE_CREATED_ASC" }
      ]}
      onSortChange={selected => {
        setSortValue(selected);
        console.log(`Sort option changed to ${selected}.`);
      }}
      filterControl={filterControl}
    />
  );

  function renderItem(item) {
    const {
      id,
      name,
      email,
      isAdmin,
      account_status,
      account_plan,
      account_price,
      account_freq,
      ltv,
      update_time,
      create_time,
      tags
    } = item;
    const media = <Avatar customer size="medium" name={id} />;
    const shortcutActions = null;

    const accountPlan = `${account_plan}`;
    const accountPrice = toCurrency(account_price);
    const accountPayment = `${accountPrice}/${account_freq}`;

    const ltvDisplay = toCurrency(ltv);

    const accountStatusDisplay = {
      trial: {
        color: "info",
        label: "Trial"
      },
      active: {
        color: "success",
        label: "Active"
      },
      pastdue: {
        color: "attention",
        label: "Past due"
      },
      unpaid: {
        color: "critical",
        label: "Unpaid"
      },
      paused: {
        color: "default",
        label: "Paused"
      },
      canceled: {
        color: "critical",
        label: "Canceled"
      }
    };
    const accountStatus = accountStatusDisplay[account_status] || {};

    const tagsDisplay = (tags || []).map(t => <Badge>{t}</Badge>);

    return (
      <ResourceItem
        id={id}
        url={`/accounts/${id}`}
        // media={media}
        accessibilityLabel={`View details for ${email}`}
        // shortcutActions={shortcutActions}
        // persistActions
      >
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <div
            style={{
              width: "50%",
              display: "flex",
              justifyContent: "space-between"
            }}
          >
            <div style={{ width: "30%" }}>{toShortDate(update_time)}</div>

            <div style={{ width: "50%" }}>
              <h3>
                <TextStyle variation="strong">{name}</TextStyle>
              </h3>
              <div>{isAdmin ? "(Staff)" : email}</div>
            </div>

            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                width: "20%"
              }}
            >
              <Stack alignment="center" spacing="extraTight">
                <Badge status={accountStatus.color}>
                  {accountStatus.label}
                </Badge>
              </Stack>
            </div>
          </div>
          <div
            style={{
              width: "50%",
              display: "flex",
              justifyContent: "space-between"
            }}
          >
            <div style={{ width: "20%", paddingLeft: 6 }}>
              <h3>
                <TextStyle variation="strong">{accountPlan}</TextStyle>
              </h3>
              <div>{accountPayment}</div>
            </div>

            <div style={{ width: "40%", textAlign: "center" }}>
              <h3>
                <TextStyle variation="strong">{ltvDisplay}</TextStyle>
              </h3>
              {create_time ? (
                <div>
                  since&nbsp;
                  {toShortDate(create_time)}
                </div>
              ) : null}
            </div>

            <div style={{ width: "40%" }}>
              <Stack
                alignment="center"
                spacing="extraTight"
                distribution="center"
              >
                {tagsDisplay}
              </Stack>
            </div>
          </div>
        </div>
      </ResourceItem>
    );
  }

  function disambiguateLabel(key, value) {
    switch (key) {
      case "taggedWith":
        return `Tagged with ${value}`;
      default:
        return value;
    }
  }

  function isEmpty(value) {
    if (Array.isArray(value)) {
      return value.length === 0;
    }
    return value === "" || value == null;
  }
}

export default function AccountListPage() {
  const [selected, setSelected] = useState(0);

  const handleTabChange = useCallback(
    selectedTabIndex => setSelected(selectedTabIndex),
    []
  );

  const tabs = [
    {
      id: "all",
      content: "All"
    },
    {
      id: "trials",
      content: "Trials"
    },
    {
      id: "renewed",
      content: "Renewed"
    },
    {
      id: "notrenewed",
      content: "Not renewed"
    }
  ];

  return (
    <Page
      title="Accounts"
      primaryAction={{ content: "Add account" }}
      secondaryActions={[
        // {
        //   icon: () => <Icon source={ImportMinor} />,
        //   content: 'Import',
        // },
        {
          icon: () => <Icon source={ExportMinor} />,
          content: "Export"
        }
      ]}
    >
      <Layout>
        <Layout.Section>
          <Card>
            <Tabs tabs={tabs} selected={selected} onSelect={handleTabChange} />
            <AccountsResourceList />
          </Card>
        </Layout.Section>
      </Layout>
    </Page>
  );
}
