function renderItem({ label, descr, icon, url }: any) {
  return (
    <ResourceItem
      id=""
      url={url}
      media={<Icon source={icon} color="inkLighter" />}
      key={label}
    >
      <strong>{label}</strong>
      <p>{descr}</p>
    </ResourceItem>
  );
}

