import React, { useCallback, useState, useRef } from "react";
import {
  Button,
  Card,
  Layout,
  Page,
  PageActions,
  ContextualSaveBar,
  Stack,
  Tabs,
  TextField,
  TextStyle,
  ChoiceList,
  ButtonGroup,
  TextContainer,
} from "@shopify/polaris";

import { useQuery, useMutation } from "@apollo/react-hooks";

import { ALL_PRICINGS, UPDATE_PRICINGS } from "../data/queries";

const MODEL_FIXED = "fixed";
const MODEL_PERSEAT = "seat";
const YEARLY_15 = "15";
const YEARLY_2MO = "2mo";
const YEARLY_20 = "20";
const YEARLY_CUSTOM = "custom";

function ToggleRevenue({ totalRevenue, onPress }: any) {
  const message = totalRevenue
    ? "Edit how much your users will pay"
    : "This shows how much you'll get, net of transaction fees";
  return (
    <Stack alignment="center">
      <Stack.Item fill>
        <p>{message}</p>
      </Stack.Item>
      <ButtonGroup segmented>
        <Button
          pressed={!totalRevenue}
          onClick={() => {
            onPress(false);
          }}
        >
          Net revenue
        </Button>
        <Button
          pressed={totalRevenue}
          onClick={() => {
            onPress(true);
          }}
        >
          Gross revenue
        </Button>
      </ButtonGroup>
    </Stack>
  );
}

function TabMonthly() {
  return <div />;
}

function TabYearly({ yearlyModel, onChange }: any) {
  return (
    <Card.Section>
      <ChoiceList
        title="Pricing per year:"
        choices={[
          { label: "15% discount", value: YEARLY_15 },
          { label: "2-month discount", value: YEARLY_2MO },
          { label: "20% discount", value: YEARLY_20 },
          { label: "Custom prices", value: YEARLY_CUSTOM },
        ]}
        selected={yearlyModel}
        onChange={onChange}
      />
    </Card.Section>
  );
}

