function PricingForm({
  priceModel,
  tabYear,
  totalRevenue,
  handleFieldChange,
}: any) {
  let cols: Array<string> = [];
  if (priceModel.model === MODEL_FIXED) {
    cols = [
      "Name",
      `Price ${!tabYear ? "(month)" : "(year)"}`,
      // 'Price (year)',
      "User limit",
      "", // Edit,
    ];
  }

  console.log("inner priceModel", priceModel);

  return (
    <Card.Section>
      <PricingHeader cols={cols} />
      {(priceModel.prices || []).map((item: any) => (
        <PricingRow
          priceModel={priceModel}
          item={item}
          cols={cols}
          tabYear={tabYear}
          totalRevenue={totalRevenue}
          handleFieldChange={handleFieldChange}
        />
      ))}
    </Card.Section>
  );
}

function PricingHeader({ cols, tabYear }: any) {
  return (
    <div>
      {cols.map((c: string) => (
        <div
          style={{
            display: "inline-block",
            width: `${100 / cols.length}%`,
            marginBottom: 10,
            paddingRight: 15,
          }}
        >
          {c}
        </div>
      ))}
    </div>
  );
}

function PricingRow({
  priceModel,
  item,
  cols,
  tabYear,
  totalRevenue,
  handleFieldChange,
}: any) {
  const { id, name, price, priceYear, limitUsers } = item;

  console.log("xxx", id, name, price, priceYear, limitUsers, priceModel);

  const priceValue = Math.round(price * (1 - 0.049)) - 0.3 || price;
  const priceDisabled = !totalRevenue;

  let priceYearValue = priceYear;
  let priceYearDisabled = !totalRevenue;
  switch (priceModel.yearlyModel[0]) {
    case YEARLY_15:
      priceYearValue = Math.round(price * 12 * (1 - 0.15));
      priceYearDisabled = true;
      break;
    case YEARLY_2MO:
      priceYearValue = price * 10;
      priceYearDisabled = true;
      break;
    case YEARLY_20:
      priceYearValue = Math.round(price * 12 * (1 - 0.2));
      priceYearDisabled = true;
      break;
  }
  if (!totalRevenue) {
    priceYearValue = Math.round(priceYearValue * (1 - 0.049)) - 0.3;
  }
  if (`${priceYearValue}` === "NaN" || `${price * 0.1}` === "NaN") {
    priceYearValue = price;
    priceYearDisabled = true;
  }

  const priceField = tabYear ? (
    <TextField
      labelHidden
      label="Price (year)"
      value={priceYearDisabled ? `${priceYearValue}` : priceYear}
      onChange={(v) => handleFieldChange("year", id, v)}
      disabled={priceYearDisabled}
    />
  ) : (
    <TextField
      labelHidden
      label="Price (month)"
      value={priceDisabled ? `${priceValue}` : price}
      onChange={(v) => handleFieldChange("month", id, v)}
      disabled={priceDisabled}
    />
  );

  return (
    <div>
      {cols.map((c: string, j: number) => (
        <div
          style={{
            display: "inline-block",
            width: `${100 / cols.length}%`,
            marginBottom: 10,
            paddingRight: 15,
          }}
        >
          {j === 0 ? (
            <TextField
              labelHidden
              label="Name"
              value={name}
              onChange={(v) => handleFieldChange("name", id, v)}
            />
          ) : null}
          {j === 1 ? priceField : null}
          {j === 2 ? (
            <TextField
              labelHidden
              label="User limit"
              value={limitUsers}
              placeholder="(unlimited)"
              onChange={(v) => handleFieldChange("limitUsers", id, v)}
            />
          ) : null}
          {j === 3 ? (
            <div style={{ textAlign: "right" }}>
              <Button size="slim" url={`/pricing/plans/${id}`}>
                Edit details
              </Button>
            </div>
          ) : null}
        </div>
      ))}
    </div>
  );
}

function makeGraphQlParam(plan: any) {
  const _p = {
    id: plan.id,
    uid: plan.uid,
    prices: {
      month: {
        id: plan.prices.month.id,
        unit_amount_decimal: "" + plan.prices.month.unit_amount_hr * 100
      },
      year: {
        id: plan.prices.year.id,
        unit_amount_decimal: "" + plan.prices.year.unit_amount_hr * 100 * 12
      }
    },
    features: plan.features.map((f : any) => ({name: f.name, value: f.value}))
  }

  return _p;
}

function PricingTabs({ plans }: any) {
  const savedPlans = useRef(plans);

  const [isDirty, setIsDirty] = useState(false);
  const [plansData, setPlansData] = useState(savedPlans.current);
  const [updateSettings] = useMutation(UPDATE_PRICINGS);

  const handleSave = useCallback(() => {
    const plansInput = plans.Plans.map(
      (p : any) => makeGraphQlParam(p)
    );

    console.log("saving with", plans, plansInput);


    updateSettings({
      variables: { plans: plansInput },
    });
    // savedPlans.current = plans;
    setIsDirty(false);
  }, [plansData]);

  const handleFieldChange = useCallback(
    (field, id, value) => {
      // console.log("changing", field, id, value, plansData);
      const newPlansData = { ...plansData };
      newPlansData.Plans = plansData.Plans.map((p: any) => {
        if (p.id === id) {
          switch (field) {
            case "month":
              p.prices.month.unit_amount_hr = value;
              p.prices.year.unit_amount_hr = value/12*10; //TODO: fix this!
              break;
            case "year":
              p.prices.year.unit_amount_hr = value;
              break;
            case "name":
              p.name = value;
              break;
            case "limitUsers":
              p.limitUsers = value;
              break;
          }
        }

        return p;
      });
      // console.log("updated to", newPlansData);
      // const dirty = FIELDS.some(key =>
      //   checkField(key, newPlansData, savedSettings.current)
      // );
      setPlansData(newPlansData);
      setIsDirty(true);
    },
    [plansData]
  );

  console.log("data", plansData, plans);
  const prices =
    plansData && plansData.Plans
      ? plansData.Plans.map((p: any) => {
          console.log("itit", p);
          const { id, name, features } = p;
          const plan = {
            id,
            name,
            price: p.prices.month.unit_amount_hr,
            priceId: p.prices.month.id,
            priceYear: p.prices.year.unit_amount_hr,
            priceYearId: p.prices.year.id,
            limitUsers: '',
            features,
          };

          return plan;
        })
      : [];

  // console.log("prices", prices);

  // TODO: get this from somewhere
  const priceModelData = {
    model: MODEL_FIXED,
    yearlyModel: [YEARLY_2MO],
    feeFixed: 30,
    feePercent: 4.9,
    prices,
  };

  // console.log("priceModelData", priceModelData);

  const [totalRevenue, setTotalRevenue] = useState(true);
  const [tabIndex, setTabIndex] = useState(0);
  const [priceModel, setPriceModel] = useState(priceModelData);

  const handleRevenuePress = useCallback(
    (value) => setTotalRevenue(!!value),
    []
  );
  const handleTabChange = useCallback(
    (selectedTabIndex) => setTabIndex(selectedTabIndex),
    []
  );
  const handleYearlyModelChange = useCallback(
    (value) => {
      console.log("setting ");
      setPriceModel({
        ...priceModel,
        yearlyModel: value,
      });
    },
    [priceModelData]
  );

  const tabs = [
    {
      id: "monthly",
      content: "Monthly",
      accessibilityLabel: "",
    },
    {
      id: "yearly",
      accessibilityLabel: "",
      content: "Yearly",
    },
  ];
  const tabYear = tabIndex !== 0;

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave,
      }}
      discardAction={{
        onAction: () => {
          console.log("discard");
        },
      }}
    />
  ) : null;

  // console.log("priceModel", priceModel);
  return (
    <div>
      {contextualSaveBarMarkup}
        <Card>
          <Card.Section>
            <ToggleRevenue
              totalRevenue={totalRevenue}
              onPress={handleRevenuePress}
            />
          </Card.Section>
          <Tabs tabs={tabs} selected={tabIndex} onSelect={handleTabChange}>
            {tabYear ? (
              <TabYearly
                yearlyModel={priceModelData.yearlyModel}
                onChange={handleYearlyModelChange}
              />
            ) : null}
          </Tabs>

          <PricingForm
            priceModel={priceModelData}
            tabYear={tabYear}
            totalRevenue={totalRevenue}
            handleFieldChange={handleFieldChange}
          />
        </Card>

        <PricingPreview
          priceModel={priceModelData}
          tabYear={tabYear}
          totalRevenue={totalRevenue}
        ></PricingPreview>
    </div>
  );
}

export default function PricingPage() {
  let { loading, error, data } = useQuery(ALL_PRICINGS);

  if (error) {
    return <p>Error</p>;
  }

  if (loading) {
    return <p>Loading</p>;
  }

  return (
    <Page
      title="Pricing"
      primaryAction={{
        content: "Edit pricing model",
        url: "/pricing/models",
      }}
    >
      <PricingTabs plans={data} />
    </Page>
  );
}
