// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  Button,
  ChoiceList,
  Popover,
  DataTable,
  Icon
} from "@shopify/polaris";
import { BankMajorMonotone } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
          payout_rate_fixed
          payout_rate_percentage
          payout_rate_international_fixed
          payout_rate_international_percentage
          payout_statement
          payout_schedule
          customer_bill_statement
          customer_bill_phone_number
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings(
    $id: ID!
    $payout_rate_fixed: String
    $payout_rate_percentage: String
    $payout_rate_international_fixed: String
    $payout_rate_international_percentage: String
    $payout_statement: String
    $payout_schedule: String
    $customer_bill_statement: String
    $customer_bill_phone_number: String
  ) {
    updateOneSettingsItem(
      input: {
        id: $id
        update: {
          payout_rate_fixed: $payout_rate_fixed
          payout_rate_percentage: $payout_rate_percentage
          payout_rate_international_fixed: $payout_rate_international_fixed
          payout_rate_international_percentage: $payout_rate_international_percentage
          payout_statement: $payout_statement
          payout_schedule: $payout_schedule
          customer_bill_statement: $customer_bill_statement
          customer_bill_phone_number: $customer_bill_phone_number
        }
      }
    ) {
      id
      payout_rate_fixed
      payout_rate_percentage
      payout_rate_international_fixed
      payout_rate_international_percentage
      payout_statement
      payout_schedule
      customer_bill_statement
      customer_bill_phone_number
    }
  }
`;

export default function SettingsPaymentsPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading) {
    return (
      <SkeletonPage title="Payments">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  }
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord =
    data && data.settingsItems.edges.length
      ? data.settingsItems.edges[0].node
      : {};
  return <SettingsPaymentsPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function renderItem(item) {
  const { id, name, paymentType, status, optional } = item;
  const shortcutActions = null;

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-between",
        paddingTop: 8,
        paddingBottom: 8
      }}
    >
      <div style={{ width: "5%", verticalAlign: "top" }}>
        <Checkbox
          label=""
          onChange={value => {}}
          checked
          disabled={!optional}
        />
      </div>

      <div style={{ width: "40%", paddingTop: 4, paddingBottom: 4 }}>
        <h3>
          <TextStyle variation="strong">{name}</TextStyle>
        </h3>
      </div>

      <div style={{ width: "60%", paddingTop: 4, paddingBottom: 4 }}>
        {paymentType}
      </div>
    </div>
  );
}

function PopoverViewRates() {
  const [popoverActive, setPopoverActive] = useState(false);

  const togglePopoverActive = useCallback(
    () => setPopoverActive(popoverActive => !popoverActive),
    []
  );

  const activator = (
    <Button onClick={togglePopoverActive} plain>
      View rates
    </Button>
  );

  return (
    <Popover
      active={popoverActive}
      activator={activator}
      onClose={togglePopoverActive}
    >
      <DataTable
        columnContentTypes={["text", "text"]}
        headings={[]}
        rows={[
          [<strong>Domestic</strong>, "4.9% + $0.30"],
          [<strong>Rest of the world</strong>, "5.9% + $0.30"]
        ]}
      />
    </Popover>
  );
}

function SettingsPaymentsPageChild({ settingsRecord }) {
  const FIELDS = [
    // read-only
    // 'payout_rate_fixed',
    // 'payout_rate_percentage',
    // 'payout_rate_international_fixed',
    // 'payout_rate_international_percentage',
    "payout_statement",
    "payout_schedule",
    "customer_bill_statement",
    "customer_bill_phone_number"
  ];
  const settingsData = settingsRecord;

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, ...settings }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const dirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(dirty);
    },
    [settings]
  );

  const handleChoiceChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value[0];
      const dirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(dirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  return (
    <Page
      title="Payments"
      breadcrumbs={[{ content: "Settings", url: "/settings" }]}
    >
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection
          title="Payment options"
          description="Select which payment options are available to your customers."
        >
          <Card>
            <Card.Header title="Saasform payments">
              <PopoverViewRates />
              <Button url="/settings/payments/payouts">View payouts</Button>
            </Card.Header>
            <Card.Section title="Payment cards">
              <ResourceList
                items={[
                  { id: 1, name: "Visa", paymentType: "Credit card" },
                  { id: 2, name: "Mastercard", paymentType: "Credit card" },
                  {
                    id: 3,
                    name: "American Express",
                    paymentType: "Credit card"
                  },
                  { id: 4, name: "Discover", paymentType: "Credit card" },
                  { id: 5, name: "JCB", paymentType: "Credit card" },
                  { id: 6, name: "Diners Club", paymentType: "Credit card" }
                ]}
                renderItem={renderItem}
              />
            </Card.Section>
            <Card.Section title="Accelerated checkout">
              <ResourceList
                items={[
                  // {id: 1, name: 'Shop Pay', paymentType: 'Accelerated checkout'},
                  {
                    id: 2,
                    name: "Apple Pay",
                    paymentType: "Accelerated checkout"
                  },
                  {
                    id: 3,
                    name: "Google Pay",
                    paymentType: "Accelerated checkout"
                  }
                ]}
                renderItem={renderItem}
              />
            </Card.Section>
          </Card>
        </Layout.AnnotatedSection>

        <Layout.AnnotatedSection
          title="Payout details"
          description="Your earnings are deposited into this bank account. Choose the frequency of your payouts and edit the way theyre described on your bank statements."
        >
          <Card
            title="Payout bank account"
            sectioned
            secondaryFooterActions={[{ content: "Change bank account" }]}
          >
            <Stack alignment="center">
              <Icon source={BankMajorMonotone} />
              <div>
                <strong>WELLS FARGO BANK NA</strong>
                <br />
                <TextStyle variation="subdued">***** 19 (USD)</TextStyle>
              </div>
            </Stack>
          </Card>
          <Card title="Payout statement descriptor" sectioned>
            <FormLayout>
              <p>The way payouts are described on your bank statements.</p>
              <TextField
                label="Payout statement descriptor"
                placeholder="Saasform"
                value={settings.payout_statement}
                minLength={5}
                maxLength={22}
                onChange={value => handleFieldChange("payout_statement", value)}
                helpText="Must be between 5 and 22 characters"
              />
            </FormLayout>
          </Card>
          <Card title="Payout schedule" sectioned>
            <ChoiceList
              title=""
              choices={[
                {
                  label: "Every week",
                  value: "week",
                  helpText: "We'll issue payouts every Tuesday."
                },
                {
                  label: "Every month",
                  value: "month",
                  helpText:
                    "We'll issue payouts every first Tuesday of the month."
                }
              ]}
              selected={[settings.payout_schedule || "week"]}
              onChange={value => handleChoiceChange("payout_schedule", value)}
            />
          </Card>
        </Layout.AnnotatedSection>

        <Layout.AnnotatedSection
          title="Customer billing statement"
          description="Edit the way your site name and phone number appear on your customers' bank statements. This is important to reduce complaints and chargebacks."
        >
          <Card title="Payout statement descriptor" sectioned>
            <FormLayout>
              <p>The way payouts are described on your bank statements.</p>
              <FormLayout.Group>
                <TextField
                  label="Customer statement descriptor"
                  placeholder="Beautifulsaas"
                  minLength={3}
                  maxLength={17}
                  value={settings.customer_bill_statement}
                  onChange={value =>
                    handleFieldChange("customer_bill_statement", value)
                  }
                  prefix="SF *"
                  helpText="SF * will appear at the begin."
                />
                <TextField
                  label="Your phone number"
                  placeholder="(415) 123 45678"
                  value={settings.customer_bill_phone_number}
                  onChange={value =>
                    handleFieldChange("customer_bill_phone_number", value)
                  }
                />
              </FormLayout.Group>
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>

        <Layout.Section>
          <PageActions
            primaryAction={{
              content: "Save",
              onAction: handleSave,
              disabled: !isDirty
            }}
          />
        </Layout.Section>
      </Layout>
    </Page>
  );
}
