import React from "react";
import { Avatar } from "@shopify/polaris";
import md5 from "md5";

type AvatarProps = {
  email: string;
  initials: string;
};

function gravatarSource(email: string) {
  if (!email) return "";
  return `https://www.gravatar.com/avatar/${md5(
    email.trim().toLowerCase()
  )}?d=404`;
}

export function UserAvatar({ email, initials }: AvatarProps) {
  return <Avatar customer source={gravatarSource(email)} initials={initials} />;
}

export function StaffAvatar({ email, initials }: AvatarProps) {
  return <Avatar source={gravatarSource(email)} initials={initials} />;
}
