// @ts-nocheck
import React, { useState, useCallback } from "react";
import { gql } from "apollo-boost";
import { useQuery } from "@apollo/react-hooks";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  ChoiceList,
  RangeSlider,
  ResourceList,
  Filters,
  Avatar,
  TextStyle,
  Button,
  ResourceItem,
  Icon,
  Stack,
  Badge,
  Tabs
} from "@shopify/polaris";
import { ExportMinor, ImportMinor } from "@shopify/polaris-icons";

import { toShortDate } from "../lib/textUtils";
import { users } from "../data/users";

const ALL_USERS = gql`
  {
    users {
      edges {
        node {
          id
          email
          isAdmin
        }
      }
    }
  }
`;

function useQueryFake() {
  return {
    loading: false,
    error: false,
    data: {
      users
    }
  };
}

function UsersResourceList() {
  const [selectedItems, setSelectedItems] = useState([]);
  const [sortValue, setSortValue] = useState("DATE_MODIFIED_DESC");
  const [filterEmailStatus, setFilterEmailStatus] = useState("");
  const [filterStatus, setFilterStatus] = useState("");
  const [taggedWith, setTaggedWith] = useState("");
  const [queryValue, setQueryValue] = useState("");

  const handleFilterEmailStatusChange = useCallback(
    value => setFilterEmailStatus(value),
    []
  );
  const handleFilterStatusChange = useCallback(
    value => setEmailStatus(value),
    []
  );
  const handleFilterTaggedWithChange = useCallback(
    value => setTaggedWith(value),
    []
  );
  const handleQueryValueChange = useCallback(value => setQueryValue(value), []);
  const handleTaggedWithRemove = useCallback(() => setTaggedWith(""), []);
  const handleQueryValueRemove = useCallback(() => setQueryValue(""), []);
  const handleClearAll = useCallback(() => {
    handleTaggedWithRemove();
    handleQueryValueRemove();
  }, [handleQueryValueRemove, handleTaggedWithRemove]);

  const resourceName = {
    singular: "user",
    plural: "users"
  };

  const promotedBulkActions = [
    {
      content: "Resend verification email",
      onAction: () => console.log("Todo: implement bulk edit")
    },
    {
      content: "Edit users",
      onAction: () => console.log("Todo: implement bulk edit")
    }
  ];

  const bulkActions = [
    {
      content: "Enable",
      onAction: () => console.log("Todo: implement bulk add tags")
    },
    {
      content: "Disable",
      onAction: () => console.log("Todo: implement bulk add tags")
    },
    {
      content: "Add tags",
      onAction: () => console.log("Todo: implement bulk add tags")
    },
    {
      content: "Remove tags",
      onAction: () => console.log("Todo: implement bulk remove tags")
    },
    {
      content: "Delete users",
      onAction: () => console.log("Todo: implement bulk delete")
    }
  ];

  const filters = [
    {
      key: "filterStatus",
      label: "Status",
      filter: (
        <ChoiceList
          title="User status"
          titleHidden
          choices={[
            { label: "Active", value: "active" },
            { label: "Invited", value: "invited" },
            { label: "Disabled", value: "disabled" }
          ]}
          selected={filterStatus || []}
          onChange={handleFilterStatusChange}
          allowMultiple
        />
      ),
      shortcut: true
    },
    {
      key: "filterEmailStatus",
      label: "Email",
      filter: (
        <ChoiceList
          title="Email status"
          titleHidden
          choices={[
            { label: "Verified", value: "verified" },
            { label: "Not verified", value: "notverified" }
          ]}
          selected={filterEmailStatus || []}
          onChange={handleFilterEmailStatusChange}
          allowMultiple
        />
      ),
      shortcut: true
    },
    {
      key: "taggedWith",
      label: "Tagged with",
      filter: (
        <TextField
          label="Tagged with"
          value={taggedWith}
          onChange={handleFilterTaggedWithChange}
          labelHidden
        />
      ),
      shortcut: true
    }
  ];

  const appliedFilters = !isEmpty(taggedWith)
    ? [
        {
          key: "taggedWith",
          label: disambiguateLabel("taggedWith", taggedWith),
          onRemove: handleTaggedWithRemove
        }
      ]
    : [];

  const filterControl = (
    <Filters
      queryValue={queryValue}
      filters={filters}
      appliedFilters={appliedFilters}
      onQueryChange={handleQueryValueChange}
      onQueryClear={handleQueryValueRemove}
      onClearAll={handleClearAll}
    >
      <div style={{ paddingLeft: "8px" }}>
        <Button onClick={() => console.log("New filter saved")}>Save</Button>
      </div>
    </Filters>
  );

  const { loading, error, data } = useQueryFake(ALL_USERS);

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  return (
    <ResourceList
      resourceName={resourceName}
      items={data.users}
      renderItem={renderItem}
      selectedItems={selectedItems}
      onSelectionChange={setSelectedItems}
      promotedBulkActions={promotedBulkActions}
      bulkActions={bulkActions}
      sortValue={sortValue}
      sortOptions={[
        { label: "Latest update", value: "DATE_MODIFIED_DESC" },
        { label: "Newest", value: "DATE_CREATED_DESC" },
        { label: "Oldest", value: "DATE_CREATED_ASC" }
      ]}
      onSortChange={selected => {
        setSortValue(selected);
        console.log(`Sort option changed to ${selected}.`);
      }}
      filterControl={filterControl}
    />
  );

  function toCurrency(val) {
    return `$${(val / 100 || 0)
      .toString()
      .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}`;
  }

  function renderItem(item) {
    const {
      id,
      name,
      account,
      user_status,
      user_login_count,
      email,
      email_status,
      isAdmin,
      update_time,
      create_time,
      tags
    } = item;
    const media = <Avatar customer size="medium" name={id} />;
    const shortcutActions = null;

    const userStatusDisplay = {
      active: {
        color: "success",
        label: "Active"
      },
      disabled: {
        color: "warning",
        label: "Disabled"
      },
      invited: {
        color: "info",
        label: "Invited"
      }
    };
    const userStatus = userStatusDisplay[user_status] || {};
    const emailStatusDisplay = {
      verified: {
        color: "success",
        label: "Verified"
      },
      notverified: {
        color: "warning",
        label: "Not verified"
      }
    };
    const emailStatus = emailStatusDisplay[email_status] || {};

    const tagsDisplay = (tags || []).map(t => <Badge>{t}</Badge>);
    const loginDisplay = `${user_login_count || 0} logins`;

    return (
      <ResourceItem
        id={id}
        url={`/users/${id}`}
        // media={media}
        accessibilityLabel={`View details for ${email}`}
        // shortcutActions={shortcutActions}
        // persistActions
      >
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <div style={{ width: "15%" }}>{toShortDate(update_time)}</div>

          <div style={{ width: "25%" }}>
            <h3>
              <TextStyle variation="strong">{name}</TextStyle>
            </h3>
            <div>{isAdmin ? "(Staff)" : (account || {}).name}</div>
          </div>

          <div
            style={{ display: "flex", width: "10%", justifyContent: "center" }}
          >
            <Stack alignment="center" spacing="extraTight">
              <Badge status={userStatus.color}>{userStatus.label}</Badge>
            </Stack>
          </div>

          <div
            style={{ display: "flex", width: "15%", justifyContent: "center" }}
          >
            <Stack alignment="center" spacing="extraTight">
              <Badge status={emailStatus.color}>{emailStatus.label}</Badge>
            </Stack>
          </div>

          <div
            style={{ display: "flex", width: "10%", justifyContent: "center" }}
          >
            {loginDisplay}
          </div>

          <div style={{ width: "20%" }}>
            <Stack
              alignment="center"
              spacing="extraTight"
              distribution="center"
            >
              {tagsDisplay}
            </Stack>
          </div>
        </div>
      </ResourceItem>
    );
  }

  function disambiguateLabel(key, value) {
    switch (key) {
      case "taggedWith":
        return `Tagged with ${value}`;
      default:
        return value;
    }
  }

  function isEmpty(value) {
    if (Array.isArray(value)) {
      return value.length === 0;
    }
    return value === "" || value == null;
  }
}

export default function UserListPage() {
  const [selected, setSelected] = useState(0);

  const handleTabChange = useCallback(
    selectedTabIndex => setSelected(selectedTabIndex),
    []
  );

  const tabs = [
    {
      id: "all",
      content: "All"
    },
    {
      id: "invited",
      content: "Invited"
    },
    {
      id: "disabled",
      content: "Disabled"
    },
    {
      id: "notverified",
      content: "Email not verified"
    }
  ];

  return (
    <Page
      title="Users"
      // primaryAction={{content: 'Add user'}}
      secondaryActions={[
        {
          icon: () => <Icon source={ImportMinor} />,
          content: "Import"
        },
        {
          icon: () => <Icon source={ExportMinor} />,
          content: "Export"
        }
      ]}
    >
      <Layout>
        <Layout.Section>
          <Card>
            <Tabs tabs={tabs} selected={selected} onSelect={handleTabChange} />
            <UsersResourceList />
          </Card>
        </Layout.Section>
      </Layout>
    </Page>
  );
}
