import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  ResourceItem
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

import { UserAvatar, StaffAvatar } from "../lib/imageUtils";

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings($id: ID!, $json: String!) {
    updateOneSettingsItem(input: { id: $id, update: { json: $json } }) {
      id
      json
    }
  }
`;

export default function SettingsStaffPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading) {
    return (
      <SkeletonPage title="Preferences">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  }
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord =
    data && data.settingsItems.edges.length
      ? data.settingsItems.edges[0].node
      : {};
  return <SettingsStaffPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function renderItem(item) {
  const { id, name, email, lastLogin, role } = item;
  const media = <UserAvatar customer size="medium" name={id} email={email} />;
  const shortcutActions = null;

  return (
    <ResourceItem
      id={id}
      url={`/users/${id}`}
      media={media}
      accessibilityLabel={`View details for ${email}`}
      // shortcutActions={shortcutActions}
      // persistActions
    >
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <div style={{ width: "80%" }}>
          <h3>
            <TextStyle variation="strong">{name}</TextStyle>
          </h3>
          <div>
            Last login:
            {lastLogin}
          </div>
        </div>

        <div style={{ width: "20%" }}>{role}</div>
      </div>
    </ResourceItem>
  );
}

function SettingsStaffPageChild({ settingsRecord }) {
  const FIELDS = [
    // "name",  // read-only
    "title",
    "description",
    "domain",
    "domain_redirect",
    "google_analytics",
    "google_tag_manager",
    "password",
    "password_enabled",
    "password_message"
  ];
  let settingsData = {};
  try {
    settingsData = JSON.parse(settingsRecord.json);
  } catch (e) {
    // pass
  }

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, json: JSON.stringify(settings) }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const isDirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(isDirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  return (
    <Page
      title="Staff"
      primaryAction={{
        content: "Add staff user"
      }}
      breadcrumbs={[{ content: "Settings", url: "/settings" }]}
    >
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection
          title="Permissions"
          description="Manage what staff can see or do in your admin dashboard."
        >
          <Card title="Owner">
            {renderItem({
              id: 110,
              name: "Emanuele Cesena",
              email: "emanuele.cesena@gmail.com",
              lastLogin: "Friday, June 26, 2020, 10:12 pm -0700"
            })}
          </Card>
          <Card title="Staff users">
            <div style={{ paddingTop: 16, paddingBottom: 16 }}>
              {renderItem({
                id: 101,
                name: "Pancrazio Auteri",
                email: "pancrazio.auteri@gmail.com",
                lastLogin: "Tuesday, May 26, 2020, 8:44 am -0700",
                role: "Full access"
              })}
              {renderItem({
                id: 108,
                name: "Davide Vernizzi",
                email: "davide.vernizzi@gmail.com",
                lastLogin: "Saturday, November 2, 2019, 2:38 pm -0700",
                role: "Developer access"
              })}
            </div>
          </Card>
        </Layout.AnnotatedSection>
      </Layout>
    </Page>
  );
}
