// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  Select,
  Button
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
          name
          email
          email_name
          admin_email
          admin_email_name
          industry
          legal_name
          phone_country
          phone_number
          address_line1
          address_line2
          address_city
          address_zip
          address_state
          address_country
          pay_with_balance
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings(
    $id: ID!
    $name: String
    $email: String
    $email_name: String
    $admin_email: String
    $admin_email_name: String
    $industry: String
    $legal_name: String
    $phone_country: String
    $phone_number: String
    $address_line1: String
    $address_line2: String
    $address_city: String
    $address_zip: String
    $address_state: String
    $address_country: String
    $pay_with_balance: Boolean
  ) {
    updateOneSettingsItem(
      input: {
        id: $id
        update: {
          name: $name
          email: $email
          email_name: $email_name
          admin_email: $admin_email
          admin_email_name: $admin_email_name
          industry: $industry
          legal_name: $legal_name
          phone_country: $phone_country
          phone_number: $phone_number
          address_line1: $address_line1
          address_line2: $address_line2
          address_city: $address_city
          address_zip: $address_zip
          address_state: $address_state
          address_country: $address_country
          pay_with_balance: $pay_with_balance
        }
      }
    ) {
      id
      name
      email
      email_name
      admin_email
      admin_email_name
      industry
      legal_name
      phone_country
      phone_number
      address_line1
      address_line2
      address_city
      address_zip
      address_state
      address_country
      pay_with_balance
    }
  }
`;

export default function SettingsGeneralPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading) {
    return (
      <SkeletonPage
        title="General"
        breadcrumbs={[{ content: "Settings", url: "/settings" }]}
      >
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  }
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord =
    data && data.settingsItems.edges.length
      ? data.settingsItems.edges[0].node
      : {};
  return <SettingsGeneralPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function SettingsGeneralPageChild({ settingsRecord }) {
  const FIELDS = [
    "name",
    "email",
    "email_name",
    "admin_email",
    "admin_email_name",
    "industry",
    "legal_name",
    "phone_country",
    "phone_number",
    "address_line1",
    "address_line2",
    "address_city",
    "address_zip",
    "address_state",
    "address_country",
    "pay_with_balance"
  ];
  const settingsData = settingsRecord;

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, ...settings }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const dirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(dirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  const industryOptions = [
    { label: "(select)", value: "" },
    { label: "Finance", value: "finance" },
    { label: "Internet", value: "internet" },
    { label: "Security", value: "security" }
  ];

  return (
    <Page
      title="General"
      breadcrumbs={[{ content: "Settings", url: "/settings" }]}
    >
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection title="Details" description="">
          <Card>
            <Card.Section>
              <FormLayout>
                <TextField
                  label="Name"
                  // maxLength={70}
                  // showCharacterCount
                  value={settings.name}
                  onChange={value => handleFieldChange("name", value)}
                />
                <Select
                  label="Industry"
                  options={industryOptions}
                  onChange={value => handleFieldChange("industry", value)}
                  value={settings.industry || ""}
                />
              </FormLayout>
            </Card.Section>
            <Card.Section title="Saasform contact">
              <FormLayout>
                <p>The email we'll use to send you notifications.</p>
                <FormLayout.Group>
                  <TextField
                    label="To (optional)"
                    placeholder="Beautifulsaas Team"
                    value={settings.admin_email_name}
                    onChange={value =>
                      handleFieldChange("admin_email_name", value)
                    }
                  />
                  <TextField
                    label="Admin email"
                    placeholder="admin@beautifulsaas.com"
                    value={settings.admin_email}
                    onChange={value => handleFieldChange("admin_email", value)}
                  />
                </FormLayout.Group>
              </FormLayout>
            </Card.Section>
            <Card.Section title="Customers contact">
              <FormLayout>
                <p>
                  The email we'll use to send notifications to your customers on
                  your behalf.
                </p>
                <FormLayout.Group>
                  <TextField
                    label="From (optional)"
                    placeholder="Beautifulsaas Team"
                    value={settings.email_name}
                    onChange={value => handleFieldChange("email_name", value)}
                  />
                  <TextField
                    label="Customer-facing email"
                    placeholder="hello@beautifulsaas.com"
                    value={settings.email}
                    onChange={value => handleFieldChange("email", value)}
                  />
                </FormLayout.Group>
              </FormLayout>
            </Card.Section>
          </Card>
        </Layout.AnnotatedSection>

        <Layout.AnnotatedSection
          title="Legal name and address"
          description="This address will appear on your invoices."
        >
          <Card sectioned>
            <FormLayout>
              <TextField
                label="Legal name of business"
                value={settings.legal_name}
                onChange={value => handleFieldChange("legal_name", value)}
              />
              <TextField
                label="Phone"
                value={settings.phone_number}
                onChange={value => handleFieldChange("phone_number", value)}
              />
              <TextField
                label="Street"
                value={settings.address_line1}
                onChange={value => handleFieldChange("address_line1", value)}
              />
              <TextField
                label="Apartment, suite, etc. (optional)"
                value={settings.address_line2}
                onChange={value => handleFieldChange("address_line2", value)}
              />
              <FormLayout.Group>
                <TextField
                  label="City"
                  value={settings.address_city}
                  onChange={value => handleFieldChange("address_city", value)}
                />
                <TextField
                  label="Postal / ZIP code"
                  value={settings.address_zip}
                  onChange={value => handleFieldChange("address_zip", value)}
                />
              </FormLayout.Group>
              <FormLayout.Group>
                <Select
                  label="Country / Region"
                  disabled
                  options={[{ label: "United States", value: "US" }]}
                />
                <Select
                  label="State"
                  options={[
                    { label: "(select)", value: "" },
                    { label: "California", value: "CA" },
                    { label: "New York", value: "NY" },
                    { label: "Oregon", value: "OR" }
                  ]}
                  onChange={value => handleFieldChange("address_state", value)}
                  value={settings.address_state || ""}
                />
              </FormLayout.Group>
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.AnnotatedSection
          title="Payment methods"
          description="Manage how you pay your bills."
        >
          <Card sectioned>
            <Checkbox
              label="Use balance to pay your bills"
              onChange={value => handleFieldChange("pay_with_balance", value)}
              checked={settings.pay_with_balance}
            />
          </Card>
          <Card
            sectioned
            secondaryFooterActions={[{ content: "Replace credit card" }]}
          >
            <Stack alignment="center">
              <Stack.Item>
                <strong>American Express</strong>
                <br />
                Ending in 1007
              </Stack.Item>
              <Badge status="success">Active</Badge>
            </Stack>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.AnnotatedSection
          title="Plan details"
          description="See your current plan. Compare plans with different features and rates."
        >
          <Card sectioned primaryFooterAction={{ content: "Upgrade plan" }}>
            <Stack distribution="fill">
              <Stack.Item>
                <strong>Member since</strong>
                <br />
                Oct 26, 2019
              </Stack.Item>
              <Stack.Item>
                <strong>Current plan</strong>
                <br />
                Pro
              </Stack.Item>
              <Stack.Item>
                <strong>Status</strong>
                <br />
                Active
              </Stack.Item>
            </Stack>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.Section>
          <PageActions
            primaryAction={{
              content: "Save",
              onAction: handleSave,
              disabled: !isDirty
            }}
          />
        </Layout.Section>
      </Layout>
    </Page>
  );
}
