import { Component } from "react";

export function toCurrency(dollarCents: any) {
  return `$${(Math.floor(dollarCents / 100) || 0)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}`;
}

// TODO: make robust
export function toCurrencyWithCents(dollars: any) {
  return (
    (dollars >= 0 ? "$" : "-$") +
    Math.abs(dollars)
      .toFixed(2)
      .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
  );
}

export function toShortDate(dateStr: any) {
  const months: any = {
    0: "Jan",
    1: "Feb",
    2: "Mar",
    3: "Apr",
    4: "May",
    5: "Jun",
    6: "Jul",
    7: "Aug",
    8: "Sep",
    9: "Oct",
    10: "Nov",
    11: "Dec"
  };

  // '2020-06-12 13:26:24'
  if (!dateStr) return "";
  const ts = Date.parse(dateStr);
  if (!ts) return "";

  const now = new Date(Date.now());
  const d = new Date(ts);

  // console.log(ts)
  // console.log(Date.now())

  // console.log(d.getFullYear());
  // console.log(now.getFullYear());
  // console.log(d.getMonth());
  // console.log(now.getMonth());
  // console.log(d.getDate());
  // console.log(now.getDate());

  if (
    d.getFullYear() === now.getFullYear() &&
    d.getMonth() === now.getMonth() &&
    d.getDate() === now.getDate()
  ) {
    return "Today";
  }
  if (d.getFullYear() === now.getFullYear()) {
    return `${months[d.getMonth()]} ${d.getDate()}`;
  }
  return `${months[d.getMonth()]} ${d.getDate()}, ${d.getFullYear()}`;
}
