// @ts-nocheck
import React, { useState, useCallback } from "react";
import { useParams } from "react-router-dom";
import { gql } from "apollo-boost";
import { useQuery } from "@apollo/react-hooks";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  ResourceItem,
  TextStyle,
  Stack,
  Button,
  List,
  TextContainer
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";

import { UserAvatar, StaffAvatar } from "../lib/imageUtils";
import { users } from "../data/users";

const QUERY_USER = gql`
  query GetUser($id: ID!) {
    user(id: $id) {
      email
      isAdmin
    }
  }
`;

function useQueryFake(query, { variables: { id } }) {
  return {
    loading: false,
    error: false,
    data: {
      user: users.filter(u => u.id == id)[0] || users[0]
    }
  };
}

function UserDetails({ user }) {
  return (
    <Card title="User details">
      <Card.Section>
        <Stack>
          <UserAvatar email={user.email} />
          <p>{user.name}</p>
        </Stack>
      </Card.Section>
    </Card>
  );
}

function AccountDetails({ user }) {
  return (
    <Card title="Account">
      <ResourceList
        items={user.accounts || [user.account]}
        renderItem={({ id, name }) => (
          <ResourceItem id={id} url={`/accounts/${id}`} media={null}>
            {name}
          </ResourceItem>
        )}
      />
    </Card>
  );
}

function UserTimeline({ user }) {
  const [note, setNote] = useState("");

  const handleNoteChange = useCallback(value => setNote(value), []);

  const staff = <StaffAvatar email="" initials="EC" />;

  return (
    <Card title="Timeline" sectioned>
      <div style={{ marginBottom: 32 }}>
        <Stack alignment="center">
          {staff}
          <Stack.Item fill>
            <TextField
              placeholder="Add a note"
              fill
              value={note}
              onChange={handleNoteChange}
              connectedRight={<Button disabled={note === ""}>Post</Button>}
            />
          </Stack.Item>
        </Stack>
      </div>
      <List type="bullet">
        {(user.timeline || []).map((item, i) => (
          <List.Item key={i}>
            {toShortDate(item.create_time)} -{item.description}
          </List.Item>
        ))}
      </List>
    </Card>
  );
}

function UserSettings({ user }) {
  return (
    <Card title="Settings">
      <Card.Section title="Login information">
        <Button plain>{user.email}</Button>
        <br />
        <Button plain>Google</Button>
        <br />
      </Card.Section>
      <Card.Section title="Security">
        <p></p>
      </Card.Section>
    </Card>
  );
}

function UserActions({ user }) {
  return (
    <Card title="Privacy" subdued>
      <Card.Section title="Request user data">
        <TextContainer>
          <p>
            Get a copy of this user's data by email so you can forward it to
            them.
          </p>
          <Button size="slim">Request user data</Button>
        </TextContainer>
      </Card.Section>
      <Card.Section title="Erase personal data">
        <TextContainer>
          <p>
            Ask to remove this user's personal information from the database.
          </p>
          <Button size="slim">Erase personal data</Button>
        </TextContainer>
      </Card.Section>
    </Card>
  );
}

function UserTags({ user }) {
  return (
    <Card
      title="Tags"
      subdued
      sectioned
      actions={[{ content: "View all tags" }]}
    >
      <TextField placeholder="VIP, pro, bot" />
    </Card>
  );
}

export default function UserPage() {
  const { id } = useParams();
  const { loading, error, data } = useQueryFake(QUERY_USER, {
    variables: { id: parseInt(id) }
  });

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  const { user } = data;

  return (
    <Page
      breadcrumbs={[{ content: "Users", url: "/users" }]}
      title={user.name}
      // primaryAction={{content: 'Save', disabled: true}}
      secondaryActions={[
        {
          content: "Send password reset email",
          icon: InviteMinor
        }
      ]}
      pagination={{
        hasPrevious: id < 110,
        hasNext: id > 101
      }}
    >
      <Layout>
        <Layout.Section>
          <UserDetails user={user} />
          <AccountDetails user={user} />
          <UserTimeline user={user} />
          <PageActions
            primaryAction={{ content: "Save" }}
            secondaryActions={[{ content: "Delete", destructive: true }]}
          />
        </Layout.Section>
        <Layout.Section secondary>
          <UserSettings user={user} />
          <UserActions user={user} />
          <UserTags user={user} />
        </Layout.Section>
      </Layout>
    </Page>
  );
}
