// @ts-nocheck
import React, { useState, useCallback, useRef } from "react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Badge,
  Thumbnail,
  Avatar,
  PageActions,
  ResourceList,
  TextStyle,
  Stack,
  TextContainer,
  DisplayText,
  MediaCard,
  VideoThumbnail,
  Checkbox,
  ContextualSaveBar,
  Loading,
  SkeletonPage,
  SkeletonDisplayText,
  SkeletonBodyText,
  Select,
  Button,
  ChoiceList
} from "@shopify/polaris";
import { InviteMinor } from "@shopify/polaris-icons";
import { gql } from "apollo-boost";
import { useQuery, useMutation } from "@apollo/react-hooks";

const QUERY_SETTINGS = gql`
  {
    settingsItems(filter: { category: { eq: "website" } }) {
      edges {
        node {
          id
          signup_billing_form
          signup_email_verification
          signup_email_social_verification
          billing_form_full_name
          billing_form_company_name
          billing_form_address_line2
          billing_form_phone_number
        }
      }
    }
  }
`;

const UPDATE_SETTINGS = gql`
  mutation UpdateSettings(
    $id: ID!
    $signup_billing_form: String
    $signup_email_verification: String
    $signup_email_social_verification: Boolean
    $billing_form_full_name: String
    $billing_form_company_name: String
    $billing_form_address_line2: String
    $billing_form_phone_number: String
  ) {
    updateOneSettingsItem(
      input: {
        id: $id
        update: {
          signup_billing_form: $signup_billing_form
          signup_email_verification: $signup_email_verification
          signup_email_social_verification: $signup_email_social_verification
          billing_form_full_name: $billing_form_full_name
          billing_form_company_name: $billing_form_company_name
          billing_form_address_line2: $billing_form_address_line2
          billing_form_phone_number: $billing_form_phone_number
        }
      }
    ) {
      id
      signup_billing_form
      signup_email_verification
      signup_email_social_verification
      billing_form_full_name
      billing_form_company_name
      billing_form_address_line2
      billing_form_phone_number
    }
  }
`;

export default function SettingsCheckoutPage() {
  const { loading, error, data } = useQuery(QUERY_SETTINGS);

  if (loading) {
    return (
      <SkeletonPage title="Preferences">
        <Loading />
        <Layout>
          <Layout.Section>
            <Card sectioned>
              <TextContainer>
                <SkeletonDisplayText size="small" />
                <SkeletonBodyText lines={9} />
              </TextContainer>
            </Card>
          </Layout.Section>
        </Layout>
      </SkeletonPage>
    );
  }
  // TODO - removed so we can have a preview of the page
  // if (error)
  //   return (
  //     <Page title="Preferences">
  //       <Layout>
  //         <Layout.Section>
  //           <p>Ops, something went wrong. Refresh to try again.</p>
  //         </Layout.Section>
  //       </Layout>
  //     </Page>
  //   );

  const settingsRecord =
    data && data.settingsItems.edges.length
      ? data.settingsItems.edges[0].node
      : {};
  return <SettingsCheckoutPageChild settingsRecord={settingsRecord} />;
}

function checkField(key, settings, savedSettings) {
  if (!savedSettings[key] && !settings[key]) return false;
  return savedSettings[key] !== settings[key];
}

function SettingsCheckoutPageChild({ settingsRecord }) {
  const FIELDS = [
    "signup_billing_form",
    "signup_email_verification",
    "signup_email_social_verification",
    "billing_form_full_name",
    "billing_form_company_name",
    "billing_form_address_line2",
    "billing_form_phone_number"
  ];
  const settingsData = settingsRecord;

  const [updateSettings] = useMutation(UPDATE_SETTINGS);
  const savedSettings = useRef({
    ...settingsData
  });

  const [isDirty, setIsDirty] = useState(false);
  const [settings, setSettings] = useState(savedSettings.current);

  const handleDiscard = useCallback(() => {
    setSettings(savedSettings.current);
    setIsDirty(false);
  }, []);
  const handleSave = useCallback(() => {
    updateSettings({
      variables: { id: settingsRecord.id, ...settings }
    });
    savedSettings.current = settings;
    setIsDirty(false);
  }, [settings]);

  const handleFieldChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value;
      const dirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(dirty);
    },
    [settings]
  );

  const handleChoiceChange = useCallback(
    (field, value) => {
      const newSettings = { ...settings };
      newSettings[field] = value[0];
      const dirty = FIELDS.some(key =>
        checkField(key, newSettings, savedSettings.current)
      );
      setSettings(newSettings);
      setIsDirty(dirty);
    },
    [settings]
  );

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave
      }}
      discardAction={{
        onAction: handleDiscard
      }}
    />
  ) : null;

  const industryOptions = [
    { label: "Finance", value: "finance" },
    { label: "Internet", value: "internet" },
    { label: "Security", value: "security" }
  ];

  return (
    <Page
      title="Signup and checkout"
      breadcrumbs={[{ content: "Settings", url: "/settings" }]}
    >
      {contextualSaveBarMarkup}
      <Layout>
        <Layout.AnnotatedSection
          title="Signup"
          description="Customize the signup experience, including billing and email verification steps."
        >
          <Card>
            <Card.Section title="Billing during signup">
              <ChoiceList
                title=""
                choices={[
                  {
                    label: "Hidden (default)",
                    value: "hidden",
                    helpText:
                      "Don't ask for credit card info during signup (if free plan or free trial available)"
                  },
                  {
                    label: "Optional",
                    value: "optional",
                    helpText:
                      "Ask for credit card info during signup, but let user skip (if free plan or free trial available)"
                  },
                  {
                    label: "Required",
                    value: "required",
                    helpText:
                      "Always require credit card info during signup, even for free plan or free trials."
                  }
                ]}
                selected={[settings.signup_billing_form || "hidden"]}
                onChange={value =>
                  handleChoiceChange("signup_billing_form", value)
                }
              />
            </Card.Section>
            <Card.Section title="Email verification">
              <ChoiceList
                title=""
                choices={[
                  {
                    label: "Required",
                    value: "required",
                    helpText:
                      "Require users to verify their email before their first login."
                  },
                  {
                    label: "Optional (default)",
                    value: "optional",
                    helpText:
                      "Send users a verification email, but let them log in without requiring verification"
                  },
                  {
                    label: "Disabled",
                    value: "disabled",
                    helpText: "Don't send any verification email"
                  }
                ]}
                selected={[settings.signup_email_verification || "optional"]}
                onChange={value =>
                  handleChoiceChange("signup_email_verification", value)
                }
              />
            </Card.Section>
            <Card.Section title="Email verification from socials">
              <Checkbox
                label="Force sending verification email when already verified on socials (default: no)"
                onChange={value =>
                  handleFieldChange("signup_email_social_verification", value)
                }
                checked={settings.signup_email_social_verification}
                helpText="Users that sign up via Google or Facebook often have their email already verified."
              />
            </Card.Section>
          </Card>
        </Layout.AnnotatedSection>

        <Layout.AnnotatedSection
          title="Checkout form"
          description="Customize the checkout form that users will see to input their billing and credit card information."
        >
          <Card>
            <Card.Section>
              <ChoiceList
                title="Full name"
                choices={[
                  {
                    label: "Require last name only (default)",
                    value: "require_last_name"
                  },
                  {
                    label: "Require first and last name",
                    value: "require_full_name"
                  }
                ]}
                selected={[
                  settings.billing_form_full_name || "require_last_name"
                ]}
                onChange={value =>
                  handleChoiceChange("billing_form_full_name", value)
                }
              />
            </Card.Section>
            <Card.Section>
              <ChoiceList
                title="Company name"
                choices={[
                  {
                    label: "Hidden",
                    value: "hidden"
                  },
                  {
                    label: "Optional (default)",
                    value: "optional"
                  },
                  {
                    label: "Required",
                    value: "required"
                  }
                ]}
                selected={[settings.billing_form_company_name || "optional"]}
                onChange={value =>
                  handleChoiceChange("billing_form_company_name", value)
                }
              />
            </Card.Section>
            <Card.Section>
              <ChoiceList
                title="Address line 2 (apartment, unit, etc.)"
                choices={[
                  {
                    label: "Hidden",
                    value: "hidden"
                  },
                  {
                    label: "Optional (default)",
                    value: "optional"
                  },
                  {
                    label: "Required",
                    value: "required"
                  }
                ]}
                selected={[settings.billing_form_address_line2 || "optional"]}
                onChange={value =>
                  handleChoiceChange("billing_form_address_line2", value)
                }
              />
            </Card.Section>
            <Card.Section>
              <ChoiceList
                title="Phone number (it won't be verified)"
                choices={[
                  {
                    label: "Hidden",
                    value: "hidden"
                  },
                  {
                    label: "Optional (default)",
                    value: "optional"
                  },
                  {
                    label: "Required",
                    value: "required"
                  }
                ]}
                selected={[settings.billing_form_phone_number || "optional"]}
                onChange={value =>
                  handleChoiceChange("billing_form_phone_number", value)
                }
              />
            </Card.Section>
          </Card>
        </Layout.AnnotatedSection>
        <Layout.Section>
          <PageActions
            primaryAction={{
              content: "Save",
              onAction: handleSave,
              disabled: !isDirty
            }}
          />
        </Layout.Section>
      </Layout>
    </Page>
  );
}
